        left: 0;\n        @include light-lamp(\n          $color-white,\n          $color-border-lamp-green,\n          $color-shadow-lamp-green\n        );\n        border-radius: 0px 1px 1px 0px;\n      }\n\n      &::after {\n        content: '';\n        position: absolute;\n        top: 50%;\n        right: 6px;\n        transform: translate(-6px, -50%);\n        @include pils;\n\n        @media (max-width: 768px) {\n          content: unset;\n        }\n      }\n    }\n  }\n}\n.transition {\n  transition: ease all 0.5s;\n}\n\n.color_ {\n  &blue {\n    .slide {\n      color: var(--blue-light) !important;\n    }\n\n    .slides {\n      .lamp {\n        &.active {\n          &::before,\n          &::after {\n            @include light-lamp(\n              $color-white,\n              $color-border-lamp-blue,\n              $color-shadow-lamp-blue\n            );\n          }\n        }\n\n        &.left {\n          &::after {\n            @include light-lamp(\n              $color-white,\n              $color-border-lamp-blue,\n              $color-shadow-lamp-blue\n            );\n          }\n        }\n\n        &.right {\n          &::before {\n            @include light-lamp(\n              $color-white,\n              $color-border-lamp-blue,\n              $color-shadow-lamp-blue\n            );\n          }\n        }\n      }\n    }\n  }\n}\n"],"sourceRoot":""}]);
