), 0.11) 100%\n  );\n}\n\n@mixin styleGradientPseudoEl {\n  content: '';\n  position: absolute;\n  height: 100%;\n  width: 100px;\n  z-index: 1;\n  top: 0;\n  bottom: 0;\n}\n\n.containerContainerGradient {\n  width: 100%;\n  min-height: 50px;\n  position: relative;\n  padding: 0px;\n\n  &::after {\n    @include styleGradientPseudoEl;\n    @include linear-gradient(90deg);\n    right: 0;\n  }\n\n  &Content {\n    position: relative;\n    margin: 0px;\n    padding: 17px 15px;\n    min-height: 170px;\n    height: 1px;\n    transition: 1s ease-in-out;\n\n    &entering {\n      opacity: 0;\n    }\n\n    &entered {\n      opacity: 1;\n      padding: 17px 15px;\n      height: 1px;\n      min-height: 170px;\n    }\n\n    &exiting {\n      opacity: 0;\n      transition: 0.5s ease-in-out;\n    }\n\n    &exited {\n      opacity: 0;\n      padding: 0px;\n      height: 0px !important;\n      min-height: 0px;\n      visibility: hidden;\n    }\n\n    &::before {\n      left: 0;\n      @include styleGradientPseudoEl;\n      @include linear-gradient(-90deg);\n      z-index: -1;\n    }\n  }\n}\n\n@mixin ion-lamp {\n  content: '';\n  position: absolute;\n  background: #ffffff;\n  border: 1px solid #ffffff;\n  box-sizing: border-box;\n  top: 0;\n  bottom: 0;\n}\n\n.wrapContainerLamp,\n.wrapContainerLampAfter,\n.wrapContainerLampBefore {\n  position: relative;\n\n  --color-r: 0;\n  --color-g: 0;\n  --color-b: 0;\n\n  // reuse colors\n  &Default {\n    --color-r: 225;\n    --color-g: 225;\n    --color-b: 225;\n  }\n\n  &Red {\n    --color-r: 255;\n    --color-g: 0;\n    --color-b: 0;\n  }\n\n  &Green {\n    --color-r: 54;\n    --color-g: 214;\n    --color-b: 174;\n  }\n\n  &Blue {\n    --color-r: 31;\n    --color-g: 203;\n    --color-b: 255;\n  }\n\n  &Yellow {\n    --color-r: 255;\n    --color-g: 217;\n    --color-b: 0;\n  }\n\n  &Pink {\n    --color-r: 246;\n    --color-g: 43;\n    --color-b: 253;\n  }\n}\n\n.wrapContainerLamp {\n  &::before {\n    left: 0;\n  }\n\n  &::after {\n    right: 0;\n  }\n\n  &::before,\n  &::after {\n    @include ion-lamp;\n  }\n}\n\n.wrapContainerLampAfter::after,\n.wrapContainerLampBefore::before {\n  @include ion-lamp;\n}\n\n.wrapContainerLampBefore {\n  &::before {\n    left: 0;\n    border-radius: 2px 0px 0px 0px;\n  }\n}\n\n.wrapContainerLampAfter {\n  &::after {\n    right: 0;\n    border-radius: 0px 2px 2px 0px;\n  }\n}\n\n.containerGradientText {\n  width: 100%;\n  min-height: 50px;\n  position: relative;\n  padding: 0px 5px;\n  height: 100%;\n\n  &::after,\n  &::before {\n    @include styleGradientPseudoEl;\n  }\n\n  &::after {\n    @include linear-gradient(90deg);\n    right: 0;\n  }\n\n  &::before {\n    left: 0;\n    @include linear-gradient(-90deg);\n  }\n\n  &Content {\n    position: relative;\n    margin: 0px -5px;\n    padding: 15px;\n    overflow-x: auto;\n    overflow-y: hidden;\n  }\n}\n\n.togglingDisable {\n  .containerContainerGradientTitle {\n    cursor: default;\n  }\n\n  .containerContainerGradientContent {\n    height: auto;\n  }\n}\n\n.titleTogglingActive {\n  cursor: pointer;\n}\n\n// TODO: remove\n.containerTxs {\n  width: 100%;\n  padding: 15px;\n  display: flex;\n  width: 100%;\n  flex-direction: column;\n  gap: 15px;\n\n  &TxHash {\n    display: flex;\n    width: 100%;\n    align-items: center;\n    justify-content: space-between;\n    position: relative;\n    z-index: 1;\n  }\n\n  &RawLog {\n    position: relative;\n    z-index: 1;\n  }\n\n  &Confirmed {\n    position: relative;\n\n    &::before {\n      @include styleGradientPseudoEl;\n      @include linear-gradient(-90deg);\n      left: 0;\n      opacity: 11%;\n    }\n\n    &::after {\n      @include styleGradientPseudoEl;\n      @include linear-gradient(90deg);\n      right: 0;\n      opacity: 11%;\n    }\n  }\n\n  &Pending {\n    position: relative;\n\n    &::before {\n      @include styleGradientPseudoEl;\n      @include linear-gradient(-90deg);\n      left: 0;\n      opacity: 12%;\n    }\n\n    &::after {\n      @include styleGradientPseudoEl;\n      @include linear-gradient(90deg);\n      right: 0;\n      opacity: 12%;\n    }\n  }\n\n  &Danger {\n    position: relative;\n\n    &::after {\n      @include styleGradientPseudoEl;\n      @include linear-gradient(90deg);\n      right: 0;\n      opacity: 11%;\n    }\n\n    &::before {\n      @include styleGradientPseudoEl;\n      @include linear-gradient(-90deg);\n      left: 0;\n      opacity: 11%;\n    }\n  }\n}\n"],"sourceRoot":""}]);
