ort './variables.module.scss';\n\n.wrapper {\n  padding: $display-padding-top $display-padding-x 24px;\n\n  overflow-x: auto;\n  // TODO: remove this\n  z-index: 0;\n\n  overflow-x: auto;\n\n  &:not(.vertical) {\n    @each $color in $valid-colors {\n      &.#{$color} {\n        @include saber-horizontal($color);\n      }\n    }\n  }\n\n  &.vertical {\n    @each $color in $valid-colors {\n      &.#{$color} {\n        @include saber-vertical($color);\n      }\n    }\n  }\n\n  &.left {\n    &::after {\n      content: '';\n      display: none;\n    }\n    border-right: unset !important;\n  }\n\n  &.right {\n    &::before {\n      content: '';\n      display: none;\n    }\n    border-left: unset !important;\n  }\n\n  &.noPaddingX {\n    padding-left: unset;\n    padding-right: unset;\n  }\n}\n\n.header {\n  position: relative;\n  top: -$display-padding-top;\n}\n","@mixin saber-background($position) {\n  content: '';\n  position: absolute;\n  z-index: -1;\n  #{$position}: 0;\n\n  @if ($position == left or $position == right) {\n    top: 0;\n    height: 100%;\n    width: 100px;\n  } @else {\n    width: 100%;\n    left: 0;\n    height: 50px;\n  }\n\n  filter: blur(1px);\n\n  background: linear-gradient(\n    /* vise versa */ to #{$position},\n    rgb(0, 0, 0) 0%,\n    rgba(var(--color-r), var(--color-g), var(--color-b), 0.11) 90%,\n    rgba(var(--color-r), var(--color-g), var(--color-b), 0.15) 95%,\n    rgba(var(--color-r), var(--color-g), var(--color-b), 0.1) 100%\n  );\n}\n\n// check Storybook for more documentation\n$valid-colors: 'green', 'blue', 'purple', 'white', 'red', 'yellow', 'orange';\n$valid-positions: 'left', 'right', 'top', 'bottom';\n$saber-border-width: 2px;\n\n@mixin saber($color: 'green', $position) {\n  position: relative;\n  border-#{$position}: $saber-border-width solid white;\n\n  // box-shadow: 0 0 10px rgba(var(--color-r), var(--color-g), var(--color-b), 0.5);\n\n  @if ($color == 'green') {\n    --color-r: 54;\n    --color-g: 214;\n    --color-b: 174;\n  } @else if ($color == 'blue') {\n    --color-r: 31;\n    --color-g: 203;\n    --color-b: 255;\n  } @else if ($color == 'purple') {\n    --color-r: 246;\n    --color-g: 43;\n    --color-b: 253;\n  } @else if ($color == 'white') {\n    --color-r: 225;\n    --color-g: 225;\n    --color-b: 225;\n  } @else if ($color == 'red') {\n    --color-r: 255;\n    --color-g: 0;\n    --color-b: 0;\n  } @else if ($color == 'yellow') {\n    --color-r: 255;\n    --color-g: 217;\n    --color-b: 0;\n  } @else if ($color == 'orange') {\n    --color-r: 255;\n    --color-g: 92;\n    --color-b: 0;\n  } @else {\n    @error \"Invalid color: #{$color}\";\n  }\n\n  @if ($position == left or $position == top) {\n    &::before {\n      @include saber-background($position);\n    }\n  } @else if ($position == right or $position == bottom) {\n    &::after {\n      @include saber-background($position);\n    }\n  } @else {\n    @error \"Invalid position: #{$position}\";\n  }\n}\n\n@mixin saber-horizontal($color) {\n  @include saber($color, left);\n  @include saber($color, right);\n}\n\n@mixin saber-vertical($color) {\n  @include saber($color, top);\n  @include saber($color, bottom);\n}\n","@import '../saber/index.module.scss';\n\n@import '../Display/Display.module.scss';\n\n.noPaddingWrapper {\n  $offset: -$display-padding-x - $saber-border-width;\n  position: relative;\n  left: $offset;\n  margin-right: $offset;\n}\n\n.displayTitle {\n  min-height: 70px;\n  padding: 10px 0;\n  font-size: 20px;\n  display: flex;\n  align-items: center;\n\n  position: relative;\n  padding-left: $display-padding-x;\n\n  @each $color in $valid-colors {\n    &.#{$color} {\n      @include saber($color, left);\n    }\n  }\n\n  &Content {\n    display: flex;\n    align-items: center;\n    min-height: 32px;\n    gap: 0 10px;\n    line-height: 25px;\n    width: 100%;\n    transition: 1.5s ease-in-out;\n    opacity: 0;\n\n    .content {\n      margin-left: auto;\n    }\n\n    &entering {\n      opacity: 0;\n      transition: 1ms ease-in-out;\n    }\n\n    &entered {\n      opacity: 1;\n    }\n\n    &exiting {\n      opacity: 0;\n      transition: 1ms ease-in-out;\n    }\n\n    &exited {\n      opacity: 1;\n    }\n  }\n}\n\n.imageWrapper {\n  img {\n    border-radius: 50%;\n    height: 70px;\n    width: 70px;\n  }\n\n  &.large {\n    img {\n      height: 112px;\n      width: 112px;\n    }\n  }\n}\n\n// .image {\n//   .circle {\n//     display: flex;\n//     padding: 5px;\n//     align-items: center;\n//     justify-content: center;\n//     border-radius: 67px;\n//     background: rgba(255, 255, 255, 0.05);\n//   }\n","$display-padding-top: 23px;\n$display-padding-x: 20px;\n"],"sourceRoot":""}]);
